.allposts {
    .uploads {
        .icons {
            margin-top: 20px;
            display: flex;

            div {
                width: 120px;
                display: flex;
                flex-direction: column;

                span {
                    display: flex;
                    align-self: center;
                }

                hr {
                    width: 100%;
                    background-color: white;
                    margin: 0;

                }

            }


        }

    }
}

.allposts {
    width: 380px;
    height: 700px;
    border-top: 1px solid rgba(245, 245, 245, 0.37);
    border-bottom: 1px solid rgba(245, 245, 245, 0.37);

    .all {
        display: grid;
        grid-template-columns: repeat(3, 120px);
        gap: 0;

        .postimg {
            height: 100px;

            img {
                width: 120px;
                height: 100px;
                object-fit: cover;
                margin-bottom: 0;
                padding: 0;
                border: 2px solid black;
            }
        }
    }
}