.profile {
    width: 360px;
    height: 600px;
    overflow-y: scroll;
    overflow-x: hidden;

    .profileTop {
        display: flex;
        justify-content: space-between;

        .profileTopLeft {
            margin-left: 5px;

            .username {
                font-weight: 600;
                font-size: x-large;
            }

        }

        .profileTopRight {
            display: flex;
            gap: 20px;
            margin-top: 7px;

            .create {}

            .Mainsetting {

                .div {
                    width: 80px;
                    height: 1px;
                    color: black;
                }
            }
        }


    }

    .editProfile {
        margin-top: 30px;

        .profileDetail {
            display: flex;
            justify-content: space-around;

            .userProfile {
                display: flex;
                flex-direction: column;
                justify-content: center;
                align-content: center;

                .profileimg {
                    width: 70px;
                    height: 70px;
                    border-radius: 50%;
                    display: flex;
                    align-self: center;
                    object-fit: cover;
                }

                span {
                    font-size: small;
                }
            }

            .posts {
                margin: 12px;

                .noOfPosts {
                    font-weight: bold;
                }

                div {
                    text-align: center;
                }
            }

            .followers {
                margin: 12px;

                text-align: center;

                .noOfFollower {
                    font-weight: bold;
                }

                div {
                    color: white;

                }
            }

            .following {
                margin: 12px;

                .noOfFollowing {
                    font-weight: bold;
                }

                div {
                    text-align: center;
                    color: white;
                }
            }
        }

        .edit {
            display: flex;
            margin: 10px;
            justify-content: space-between;

            button {
                width: 300px;
                background-color: rgb(236, 230, 230);
                border: none;
                border-radius: 5px;
                padding: 5px;
                font-weight: bold;
                font-size: medium;
            }
        }
    }

    .uploads {
        .icons {
            margin-top: 20px;
            display: flex;

            div {
                width: 120px;
                display: flex;
                flex-direction: column;

                span {
                    display: flex;
                    align-self: center;
                }

                hr {
                    width: 100%;
                    background-color: white;
                    margin: 0;
                }
            }
        }
    }

    .loading {
        font-size: xx-large;
    }
}